<template>
    <div class="m-dashboard-publish-house">
        <!--   -->
        <!-- 
            localDraft : 
            infoEnable : 
            markdownEnable : markdown
            excerptEnable : 
            tagEnable : 
            notifyEnable : 
            bannerEnable : ,
         -->
        <boilerplate
            v-if="loaded"
            :name="name"
            :type="type"
            :post="post"
            :meta="meta"
            :extend="extend"
            :localDraft="true"
            :infoEnable="true"
            :contentEnable="true"
            :markdownEnable="false"
            :excerptEnable="true"
            :tagEnable="false"
            :notifyEnable="false"
            :bannerEnable="false"
            @publish="toPublish"
            @draft="toDraft"
        >
            <!--   -->
            <template>
                <!-- 1. -->
                <el-form-item label="" class="m-house-coord">
                    <el-row :gutter="20">
                        <el-col :span="8"
                            ><el-input
                                v-model="post.post_meta.num"
                                placeholder=""
                                @change="computeArea(post.post_meta.num)"
                                ><template slot="append"></template></el-input
                            ></el-col
                        >
                        <el-col :span="8"
                            ><el-input
                                v-model="post.post_meta.area"
                                placeholder=""
                                ><template slot="append"
                                    ></template
                                ></el-input
                            ></el-col
                        >
                        <el-col :span="8"
                            ><el-input
                                v-model="post.post_meta.level"
                                placeholder=""
                                :min="1"
                                :max="99"
                                ><template slot="prepend"
                                    ></template
                                ></el-input
                            ></el-col
                        >
                    </el-row>
                </el-form-item>

                <!-- 2. -->
                <el-form-item label="" class="m-house-coord">
                    <el-row :gutter="20">
                        <el-col :span="8">
                            <el-select
                                v-model="post.post_subtype"
                                filterable
                                placeholder=""
                            >
                                <el-option
                                    v-for="item in maps"
                                    :key="item"
                                    :label="item"
                                    :value="item"
                                >
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="8">
                            <el-select
                                class="m-flower-rec-select"
                                v-model="post.post_meta.server"
                                filterable
                                placeholder=""
                            >
                                <el-option
                                    v-for="item in servers"
                                    :key="item"
                                    :label="item"
                                    :value="item"
                                >
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="8"
                            ><el-input
                                v-model="post.post_meta.line"
                                placeholder=""
                                ><template slot="append"></template></el-input
                            ></el-col
                        >
                    </el-row>
                </el-form-item>

                <!-- 3. -->
                <el-form-item label="">
                    <album
                        :imgList="post.post_meta.pics"
                        @albumChange="updateAlbum"
                    ></album>
                </el-form-item>

                <!-- 3. -->
                <el-form-item label="">
                    <el-switch
                        v-model="post.post_meta.hasData"
                        active-color="#13ce66"
                    ></el-switch>
                </el-form-item>
                <div class="m-publish-datalist" v-if="post.post_meta.hasData">
                    <div class="u-wrapper">
                        <el-row class="u-thead">
                            <el-col :span="6"></el-col>
                            <el-col :span="6"></el-col>
                            <el-col :span="6"></el-col>
                            <el-col :span="6"></el-col>
                        </el-row>
                        <div class="u-tbody">
                            <el-row
                                class="u-tr"
                                v-for="(data, i) in post.post_meta.blueprint"
                                :key="i"
                            >
                                <el-col class="u-type" :span="6">
                                    <el-radio
                                        v-model="data.type"
                                        label=""
                                        ></el-radio
                                    >
                                    <el-radio
                                        v-model="data.type"
                                        label=""
                                        ></el-radio
                                    >
                                </el-col>

                                <el-col :span="6"
                                    ><el-input
                                        v-model="data.desc"
                                        placeholder=""
                                    ></el-input
                                ></el-col>

                                <el-col :span="6" class="u-action">
                                    <!--  -->
                                    <input
                                        class="u-data-input"
                                        type="file"
                                        :id="'data_' + i"
                                        @change="uploadData($event, data, i)"
                                    />
                                    <el-button
                                        type="primary"
                                        @click="selectData(i)"
                                        icon="el-icon-upload2"
                                        ></el-button
                                    >
                                    <span
                                        class="u-data-ready"
                                        v-show="data.file"
                                    >
                                        <i class="el-icon-success"></i>
                                        
                                    </span>
                                </el-col>

                                <el-col :span="6" class="u-action">
                                    <!--  -->
                                    <el-button plain @click="addData(i)" icon="el-icon-circle-plus-outline"
                                        ></el-button
                                    >

                                    <!--  -->
                                    <el-button
                                        type="danger"
                                        v-if="i !== 0"
                                        @click="delData(i)"
                                        icon="el-icon-remove-outline"
                                        ></el-button
                                    >
                                </el-col>
                            </el-row>
                        </div>
                    </div>
                </div>
            </template>
        </boilerplate>
    </div>
</template>

<script>
import boilerplate from "@/components/publish/boilerplate";
import { __server } from "@jx3box/jx3box-common/js/jx3box.json";
const API = __server + "upload";
import { uploadData } from "../service/house";
import album from "@/components/publish/album.vue";
import servers from "@jx3box/jx3box-data/data/server/server_list.json";
import areas from "@jx3box/jx3box-data/data/house/area.json";
import lodash from "lodash";

export default {
    name: "house",
    props: [],
    data: function() {
        return {
            // - 
            type: "house",
            name: "",
            loaded: false,

            // - meta,
            servers,
            maps: [""],
            meta: {},

            // - 
            post: {
                ID: "", //ID
                // post_author               //,token
                // post_type:"",             //(boilerplate)
                post_subtype: "", //()
                post_title: "", //
                post_content: "", //,meta
                post_meta: {
                    server: "", //
                    line: "", //
                    num: "", //
                    area: "", //
                    level: "", //
                    pics: [], //
                    hasData: true,
                    blueprint: [
                        {
                            type: "",
                            desc: "",
                            file: "",
                        },
                    ], //
                },
                post_excerpt: "", //
                post_mode: "tinymce", //()
                post_banner: "", //,
                post_status: "", //
                // post_tags: [],            //
            },

            // - 
            extend: {
                feedEnable: false, //
                followEnable: false, //
                tencentEnable: false, //
                weiboEnable: false, //
                tuilanEnable: false, //
            },

            // 
            dialogImageUrl: "",
            dialogVisible: false,
            upload_url: API,
        };
    },
    computed: {},
    methods: {
        // 
        toPublish: function() {
            // console.log(this.build())
            this.doPublish(this.build(), this);
            // console.log(this.$store.state);
        },
        // 
        toDraft: function() {
            this.doDraft(this.build(), this);
            // console.log(this.$store.state);
        },
        // 
        init: function() {
            return this.doLoad(this);
        },
        // meta
        build: function() {
            let data = this.$store.state;
            data.post.meta_1 = data.post.post_meta.num; //
            data.post.meta_2 = data.post.post_meta.area; //
            data.post.meta_3 = data.post.post_meta.hasData; //
            data.post.meta_4 = data.post.post_meta.level; //
            return data;
        },

        // 
        selectData: function(i) {
            let fileInput = document.getElementById("data_" + i);
            fileInput.dispatchEvent(new MouseEvent("click"));
        },
        uploadData: function(e, item, i) {
            let formdata = new FormData();
            let file = e.target.files[0];
            formdata.append("file", file);
            uploadData(formdata, this).then((res) => {
                item.file = res.data.data.list[0];
                this.$message({
                    message: res.data.msg,
                    type: "success",
                });
            });
        },
        // 
        addData: function(i) {
            // 10
            if (this.post.post_meta.blueprint.length > 10) {
                this.$message.error("10");
                return;
            }
            this.post.post_meta.blueprint.push({
                type: "",
                desc: "",
                file: "",
            });
        },
        // 
        delData: function(i) {
            this.post.post_meta.blueprint.splice(i, 1);
        },

        // 
        updateAlbum: function(filelist) {
            let imglist = [];
            filelist.forEach((img) => {
                imglist.push({
                    name: img.name,
                    url: img.url,
                });
            });
            this.post.post_meta.pics = imglist;
        },

        // 
        computeArea: function(num) {
            if (num) {
                let area = lodash.get(
                    areas[this.post.post_subtype][~~num - 1],
                    "area"
                );
                this.post.post_meta.area = area;
            }
        },
    },
    mounted: function() {
        // 
        this.init().then(() => {
            console.log("Init Post:", this.post);
        });
    },
    components: {
        boilerplate,
        album,
    },
};
</script>

<style lang="less">
@import "../assets/css/publish/house.less";
</style>
