<template>
    <div class="m-publish-fb">
        <!--   -->
        <!-- 
            localDraft : 
            infoEnable : 
            markdownEnable : markdown
            excerptEnable : 
            tagEnable : 
            notifyEnable : 
            bannerEnable : ,
         -->
        <boilerplate
            v-if="loaded"
            :name="name"
            :type="type"
            :post="post"
            :meta="meta"
            :extend="extend"
            :localDraft="true"
            :infoEnable="true"
            :contentEnable="true"
            :markdownEnable="false"
            :excerptEnable="true"
            :tagEnable="false"
            :notifyEnable="true"
            :bannerEnable="true"
            @publish="toPublish"
            @draft="toDraft"
        >
            <!--   -->
            <template v-if="ready && !loading" v-loading="loading">

                <!-- 1. -->
                <el-form-item label="" v-if="zlp_list">
                    <el-radio
                        v-for="(zlp, i) in zlp_list"
                        :label="zlp"
                        border
                        :key="i"
                        v-model="post.post_meta.fb_zlp"
                        @change="optionChange(zlp)"
                        >{{ zlp }}</el-radio
                    >
                </el-form-item>

                <!-- 2. -->
                <el-form-item label="" v-if="fb_list">
                    <el-radio
                        class="u-fb-thumbnail"
                        v-for="(fb, key) in fb_list"
                        :label="key"
                        :key="key"
                        v-model="post.post_meta.fb_name"
                        @change="changeSubtype(key)"
                    >
                        <img :src="fb.icon | thumbnail(fb.icon)" :alt="key" />
                        <span>{{ key }}</span>
                    </el-radio>
                </el-form-item>

                <!-- BOSS -->
                <el-form-item label="" v-if="boss_list">
                    <el-checkbox-group v-model="post.post_meta.fb_boss">
                        <el-checkbox-button
                            v-for="(boss, i) in boss_list"
                            :label="boss.name"
                            :key="i"
                            >{{ boss.name }}</el-checkbox-button
                        >
                    </el-checkbox-group>
                </el-form-item>

                <!--  -->
                <el-form-item label="" v-if="level_list">
                    <el-checkbox-group v-model="post.post_meta.fb_level">
                        <el-checkbox
                            v-for="level in level_list"
                            :label="level.mode"
                            :key="level.mode + level.map_id"
                        ></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

            </template>
        </boilerplate>
    </div>
</template>

<script>
import boilerplate from "../components/publish/boilerplate";

// 
import { __ossMirror } from "@jx3box/jx3box-common/js/jx3box";
import fbmap from '@jx3box/jx3box-data/data/fb/fb_map.json'
import lodash from 'lodash'

export default {
    name: "fb",
    props: [],
    data: function() {
        return {
            // - 
            type: "fb",
            name: "",
            loading : true,
            loaded : false,

            // - 
            options: {
                map: fbmap,
            },

            // - meta,
            meta: {},

            // - 
            post: {
                ID: "",                      //ID
                // post_author               //,token
                // post_type:"",             //(boilerplate)
                post_subtype:"",      //()
                post_title: "",              //
                post_content: "",            //,meta
                post_meta: {                 //json
                    fb_zlp: "",
                    fb_name: "",
                    fb_boss: [],
                    fb_level: [],
                },
                post_excerpt: "",            //
                post_mode: "tinymce",        //()
                post_banner: "",             //,
                post_status: "",             //
                // post_tags: [],            //
            },

            // - 
            extend: {
                feedEnable: false,           //
                followEnable: false,         //
                tencentEnable: false,        //
                weiboEnable: false,          //
                tuilanEnable: false,         //
            },
        };
    },
    computed: {
        // 
        ready:function (){
            return Object.keys(this.options.map).length
        },
        zlp_list :function (){
            return Object.keys(this.options.map)
        },
        fb_list : function (){
            return this.options.map[this.post.post_meta.fb_zlp]['dungeon']
        },
        boss_list : function (){
            return this.fb_list[this.post.post_meta.fb_name]['detail']['boss_infos']
        },
        level_list : function (){
            return this.fb_list[this.post.post_meta.fb_name]['maps']
        },
    },
    methods: {
        // 
        toPublish: function() {
            this.doPublish(this.$store.state, this)
            // console.log(this.$store.state)
        },
        // 
        toDraft: function() {
            this.doDraft(this.$store.state, this)
            // console.log(this.$store.state)
        },
        // 
        init: function() {
            return this.doLoad(this);
        },
        // 
        optionChange : function (zlp){
            let first = Object.keys(this.options.map[zlp]['dungeon'])[0]
            this.post.post_meta.fb_name = first
        },
        // 
        changeSubtype : function (subtype){
            this.$store.commit('changeSubtype',subtype)
        }
    },
    mounted: function() {
        // 
        this.init().then(() => {
            this.loading = false
            console.log('Init Post:',this.post)
        })
},
    filters: {
        thumbnail: function(url) {
            return __ossMirror + url + '?v20200510';
        },
    },
    components: {
        boilerplate,
    },
};
</script>

<style lang="less">
@import "../assets/css/publish/fb.less";
</style>
