import { $ } from "./axios";
import lodash from "lodash";

// 
function doPublish(data, vm,skip=true) {
    return $.post(`post/publish`, data)
        .then((res) => {
            
            if(skip){
                this.$message({
                    message: res.data.msg,
                    type: "success",
                });

                setTimeout(() => {
                    location.href = '/' + data.post.post_type + "/?pid=" +  res.data.data.ID;
                }, 500);
            }

            return res;
        })
        .catch((err) => {
            console.log(err);
            vm.failCallback(err, vm);
        });
}

