<template>
    <div class="m-publish-jx3dat">
        <!--   -->
        <!-- 
            localDraft : 
            infoEnable : 
            markdownEnable : markdown
            excerptEnable : 
            tagEnable : 
            notifyEnable : 
            bannerEnable : ,
         -->
        <boilerplate
            v-if="loaded"
            :name="name"
            :type="type"
            :post="post"
            :meta="meta"
            :extend="extend"
            :localDraft="true"
            :infoEnable="true"
            :contentEnable="true"
            :markdownEnable="false"
            :excerptEnable="true"
            :tagEnable="false"
            :notifyEnable="true"
            :bannerEnable="true"
            @publish="toPublish"
            publish_text=""
            @draft="toDraft"
            draft_text=""
        >
            <!--   -->
            <template>
                <!-- 1. -->
                <el-form-item label="" class="m-jx3dat-subtypes">
                    <el-radio
                        v-for="(name, key) in options.type_map"
                        :label="key"
                        border
                        :key="key"
                        v-model="post.post_meta.type"
                        @change="changeSubtype(post.post_meta.type)"
                        >{{ name }}</el-radio
                    >
                </el-form-item>

                <!-- 2.tag -->
                <el-form-item label="" v-if="post.post_meta.type == 1">
                    <el-checkbox-group v-model="post.post_meta.tag">
                        <el-checkbox
                            v-for="item in options.tag_list"
                            :label="item"
                            :key="item"
                        ></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <!-- 3. -->
                <template v-if="post.post_meta.type == 1">
                    <el-divider content-position="left"></el-divider>
                    <div class="m-data-box">
                        <div class="m-data-header">
                            <el-button
                                class="m-data-addbutton"
                                icon="el-icon-circle-plus-outline"
                                type="primary"
                                @click="addDBM"
                                ></el-button
                            >
                            <a
                                class="m-data-help el-button el-button--success is-plain el-button--small"
                                href="https://www.jx3box.com/tool/?pid=13912"
                                target="_blank"
                            >
                                <i class="el-icon-info"></i> 
                            </a>
                        </div>

                        <el-tabs
                            v-model="activeIndex"
                            type="card"
                            closable
                            @tab-remove="delDBM"
                        >
                            <el-tab-pane
                                v-for="(item, i) in post.post_meta.data"
                                :key="i"
                                :name="i + 1 + ''"
                            >
                                <span slot="label" class="m-data-tab-label"
                                    ><i class="el-icon-box"></i
                                    >{{ item.name }}</span
                                >
                                <div class="m-data-item">
                                    <h5 class="u-title"></h5>
                                    <div class="u-group">
                                        <div class="u-subblock">
                                            <el-input
                                                v-model="item.name"
                                                :minlength="1"
                                                :maxlength="10"
                                                show-word-limit
                                                @change="checkDataName(item)"
                                                :disabled="i == 0"
                                                :placeholder="
                                                    i == 0
                                                        ? ''
                                                        : ''
                                                "
                                            >
                                                <template slot="prepend"
                                                    ><b class="u-feed"
                                                        >{{ user.name
                                                        }}{{
                                                            item.name ==
                                                            ""
                                                                ? ""
                                                                : "#" +
                                                                  item.name
                                                        }}</b
                                                    ></template
                                                >
                                            </el-input>
                                        </div>
                                        <div
                                            class="u-subblock u-status-wrapper"
                                        >
                                            <el-switch
                                                v-model="item.status"
                                                active-color="#13ce66"
                                                inactive-color="#ff4949"
                                            >
                                            </el-switch>

                                            <el-tooltip
                                                effect="dark"
                                                content=",,"
                                                placement="top"
                                            >
                                                <span class="u-status">{{
                                                    item.status
                                                        ? ""
                                                        : ""
                                                }}</span>
                                            </el-tooltip>
                                        </div>
                                    </div>
                                </div>
                                <div class="m-data-item">
                                    <h5 class="u-title"></h5>
                                    <el-input
                                        v-model="item.desc"
                                        placeholder=""
                                        maxlength="50"
                                        show-word-limit
                                    ></el-input>
                                </div>
                                <div class="m-data-item m-data-jx3dat">
                                    <h5 class="u-title"></h5>
                                    <input
                                        class="u-data-input"
                                        type="file"
                                        :id="'jx3dat_' + i"
                                    />
                                    <!-- <el-button
                                        size="small"
                                        type="primary"
                                        plain
                                        @click="selectDBM(i)"
                                        icon="el-icon-zoom-in"
                                        ></el-button
                                    > -->
                                    <el-button
                                        size="small"
                                        type="primary"
                                        plain
                                        @click="uploadDBM(item, i)"
                                        icon="el-icon-s-promotion"
                                        ></el-button
                                    >
                                    <el-input
                                        class="u-fileurl"
                                        placeholder=""
                                        :disabled="true"
                                        :value="item.file"
                                        v-if="item.file"
                                        ><template slot="prepend"
                                            ><span class="u-status">
                                                </span
                                            ></template
                                        ><template slot="append"
                                            ><span
                                                class="u-copy"
                                                v-clipboard:copy="item.file"
                                                v-clipboard:success="onCopy"
                                                v-clipboard:error="onError"
                                            >
                                                <i
                                                    class="el-icon-document-copy"
                                                ></i
                                                ><span></span>
                                            </span></template
                                        >
                                    </el-input>
                                </div>
                                <!-- <div class="m-data-item">
                                    <h5 class="u-title">ID</h5>
                                    <el-input
                                        placeholder="ID"
                                        :disabled="true"
                                    >
                                    </el-input>
                                </div> -->
                            </el-tab-pane>
                        </el-tabs>
                    </div>
                    <div class="m-data-more">
                        <div class="u-more" @click="toggleMoreFeed">
                            <i
                                :class="
                                    moreFeedsVisible
                                        ? 'el-icon-arrow-up'
                                        : 'el-icon-arrow-down'
                                "
                            ></i
                            ><span></span>
                        </div>
                        <el-row class="u-tr" v-show="moreFeedsVisible">
                            <el-col :span="24"
                                ><el-input
                                    v-model="post.post_meta.github"
                                    placeholder="()"
                                    ><template slot="prepend"
                                        >Github</template
                                    ><template slot="append"
                                        >@github</template
                                    ></el-input
                                ></el-col
                            >
                        </el-row>
                        <el-row class="u-tr" v-show="moreFeedsVisible">
                            <el-col :span="24"
                                ><el-input
                                    v-model="post.post_meta.gitee"
                                    placeholder="()"
                                    ><template slot="prepend"
                                        >Gitee</template
                                    ><template slot="append"
                                        >@gitee</template
                                    ></el-input
                                ></el-col
                            >
                        </el-row>
                        <el-row class="u-tr" v-show="moreFeedsVisible">
                            <el-col :span="24"
                                ><el-input
                                    v-model="post.post_meta.aliyun"
                                    placeholder="()"
                                    ><template slot="prepend"
                                        >Aliyun</template
                                    ><template slot="append"
                                        >@aliyun</template
                                    ></el-input
                                ></el-col
                            >
                        </el-row>
                    </div>
                </template>

                <!-- 4. -->
                <el-form-item v-else label="" class="m-jx3dat-other">
                    <input class="u-data-input" type="file" id="otherdata" />
                    <el-button
                        type="primary"
                        icon="el-icon-s-promotion"
                        plain
                        size="medium"
                        @click="uploadDat"
                        ></el-button
                    >
                    <el-input
                        v-if="post.post_meta.down"
                        class="u-fileurl"
                        placeholder=""
                        :disabled="true"
                        :value="post.post_meta.down"
                        ><template slot="prepend"
                            ><span class="u-status">
                                </span
                            ></template
                        ><template slot="append"
                            ><span
                                class="u-copy"
                                v-clipboard:copy="post.post_meta.down || ''"
                                v-clipboard:success="onCopy"
                                v-clipboard:error="onError"
                            >
                                <i class="el-icon-document-copy"></i
                                ><span></span>
                            </span></template
                        >
                    </el-input>
                </el-form-item>
            </template>
        </boilerplate>
    </div>
</template>

<script>
import boilerplate from "../components/publish/boilerplate";

import { uploadHub, uploadData, syncRedis } from "../service/jx3dat.js";
import User from "@jx3box/jx3box-common/js/user";
import { jx3dat_types, jx3dat_tags } from "../assets/data/jx3dat.json";
import { sterilizer } from "sterilizer/index.js";

export default {
    name: "jx3dat",
    props: [],
    data: function() {
        return {
            // - 
            type: "jx3dat",
            name: "",
            loaded: false,

            // - 
            options: {
                type_map: jx3dat_types,
                tag_list: jx3dat_tags,
            },

            // - meta,
            meta: {},

            // - 
            post: {
                ID: "", //ID
                // post_author               //,token
                // post_type:"",             //(boilerplate)
                post_subtype: "1", //()
                post_title: "", //
                post_content: "", //,meta
                post_meta: {
                    //,,
                    type: "1",
                    data: [
                        {
                            name: "",
                            desc: "",
                            status: true,
                            file: "",
                            version: "",
                        },
                    ],
                    tag: [],
                    github: "",
                    gitee: "",
                    aliyun: "",
                    down: "",
                },
                post_excerpt: "", //
                post_mode: "tinymce", //()
                post_status: "", //
                post_banner: "", //,
                // post_tags: [], //
            },

            // - 
            extend: {
                feedEnable: false, //
                followEnable: false, //
                tencentEnable: false, //
                weiboEnable: false, //
                tuilanEnable: false, //
            },

            // 
            user: {},
            tempname: "",
            moreFeedsVisible: false,
            activeIndex: "1",
        };
    },
    computed: {},
    methods: {
        // 
        toPublish: function() {
            this.doPublish(this.build(), this, false).then((res) => {
                if (this.post.post_subtype == 1) {
                    syncRedis(res.data.data, this)
                        .then((redis_result) => {
                            this.finish(
                                res.data.msg,
                                res.data.data.ID,
                                this.type
                            );
                        })
                        .catch((err) => {
                            this.failCallback(err, this);
                        });
                } else {
                    this.finish(res.data.msg, res.data.data.ID, this.type);
                }
            });
        },
        // 
        toDraft: function() {
            this.doDraft(this.build(), this, false).then((res) => {
                if (this.post.post_subtype == 1) {
                    syncRedis(res.data.data, this)
                        .then((redis_result) => {
                            this.finish(
                                res.data.msg,
                                res.data.data.ID,
                                this.type
                            );
                        })
                        .catch((err) => {
                            this.failCallback(err, this);
                        });
                } else {
                    this.finish(res.data.msg, res.data.data.ID, this.type);
                }
            });
            // console.log(this.$store.state);
        },
        finish: function(msg, id, type) {
            this.$message({
                message: msg,
                type: "success",
            });
            setTimeout(() => {
                location.href = "/" + type + "/?pid=" + id;
            }, 500);
        },
        // 
        init: function() {
            return this.doLoad(this).then((data) => {
                // if(!data.post.post_meta){
                //     this.$set(this.post,'post_meta',{
                //         //,,
                //         type: "1",
                //         data: [
                //             {
                //                 name: "",
                //                 desc: "",
                //                 status: true,
                //                 file: "",
                //             },
                //         ],
                //         tag: [],
                //         github: "",
                //         gitee: "",
                //         aliyun: "",
                //         down: "",
                //     })
                // }
                // if(!data.post.post_meta.data || !data.post.post_meta.data.length){
                //     this.$set(this.post.post_meta,'data',[
                //             {
                //                 name: "",
                //                 desc: "",
                //                 status: true,
                //                 file: "",
                //             },
                //         ]
                //     )
                // }
            });
        },

        // meta
        build: function() {
            let data = this.$store.state;
            data.post.meta_1 = data.post.post_meta.tag.toString(); //
            return data;
        },

        // 
        changeSubtype: function(subtype) {
            this.$store.commit("changeSubtype", subtype);
        },
        // 
        checkDataName: function(data) {
            let name = sterilizer(data.name).removeSpace();
            name = sterilizer(name).kill();
            this.$set(data, "name", name);
        },
        // DBM
        selectDBM: function(i) {
            let fileInput = document.getElementById("jx3dat_" + i);
            fileInput.dispatchEvent(new MouseEvent("click"));
        },
        uploadDBM: function(item, i) {
            let fileInput = document.getElementById("jx3dat_" + i);
            let file = fileInput.files[0];
            if (!file) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            let formdata = new FormData();
            formdata.append("jx3dat", file);
            uploadHub(formdata, this).then((res) => {
                if (res) {
                    item.file = res.data.download_url;
                    this.$message({
                        message: "",
                        type: "success",
                    });
                    item.version = Date.now();
                }
            });
        },
        // 
        addDBM: function() {
            // 3
            if (this.post.post_meta.data.length >= 3) {
                this.$alert("3", "", {
                    confirmButtonText: "",
                });
                return;
            }

            this.post.post_meta.data.push({
                name: "",
                desc: "",
                status: true,
                file: "",
            });

            let index = this.post.post_meta.data.length + 1 + "";
            this.activeMacroIndex = index;
        },
        // 
        delDBM: function(name) {
            // this.post.post_meta.data.splice(i, 1);

            if (name == 1) {
                this.$alert(" ", "", {
                    confirmButtonText: "",
                });
                return;
            }

            if (this.post.post_meta.data.length < 2) {
                this.$alert(" ", "", {
                    confirmButtonText: "",
                });
                return;
            }

            this.$alert("", "", {
                confirmButtonText: "",
                callback: (action) => {
                    if (action == "confirm") {
                        // 
                        let i = ~~name - 1;
                        this.post.post_meta.data.splice(i, 1);
                        // focus
                        this.activeIndex = i + "";
                    }
                },
            });
        },
        // 
        selectDat: function() {
            let fileInput = document.getElementById("otherdata");
            fileInput.dispatchEvent(new MouseEvent("click"));
        },
        uploadDat: function(e) {
            let fileInput = document.getElementById("otherdata");
            let file = fileInput.files[0];
            if (!file) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            let formdata = new FormData();
            formdata.append("file", file);
            uploadData(formdata, this).then((res) => {
                this.post.post_meta.down = res.data.data.list[0];

                this.$message({
                    message: res.data.msg,
                    type: "success",
                });

                this.tempname = file.name;
            });
        },
        toggleMoreFeed: function() {
            this.moreFeedsVisible = !this.moreFeedsVisible;
        },
        onCopy: function(val) {
            this.$notify({
                title: "",
                message: "",
                type: "success",
            });
        },
        onError: function() {
            this.$notify.error({
                title: "",
                message: "",
            });
        },
    },
    mounted: function() {
        // 
        this.init().then((data) => {});
        this.user = User.getInfo();
    },
    filters: {},
    components: {
        boilerplate,
    },
};
</script>

<style lang="less">
@import "../assets/css/publish/jx3dat.less";
</style>
