<template>
    <div class="m-publish-cj">
        <!--   -->
        <!-- 
            localDraft : 
            infoEnable : 
            markdownEnable : markdown
            excerptEnable : 
            tagEnable : 
            notifyEnable : 
            bannerEnable : ,
         -->
        <boilerplate
            :name="name"
            :type="type"
            :post="post"
            :extend="extend"
            :localDraft="true"
            :infoEnable="true"
            :contentEnable="true"
            :markdownEnable="false"
            :excerptEnable="false"
            :tagEnable="false"
            :notifyEnable="false"
            :bannerEnable="false"
            @publish="toPublish"
            @draft="toDraft"
        >
            <!--   -->
            <el-form-item label="">
                <el-select
                    class="u-achievement_id"
                    v-model="post.achievement_id"
                    filterable
                    remote
                    reserve-keyword
                    :disabled="!!post.ID"
                    placeholder="///"
                    :remote-method="search_achievements_handle"
                    :loading="options.search_loading"
                >
                    <el-option
                        v-for="item in options.achievements"
                        :key="item.ID"
                        :label="item.Name"
                        :value="item.ID"
                    >
                        <div class="m-selector-item">
                            <img
                                class="u-icon"
                                :src="icon_url_filter(item.IconID)"
                                :alt="item.Name"
                            />
                            <span class="u-name" v-text="item.Name"></span>
                        </div>
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="">
                <el-rate v-model="post.level" class="u-level"></el-rate>
            </el-form-item>

            <el-form-item label="">
                <el-input
                    v-model="post.remark"
                    placeholder=""
                ></el-input>
            </el-form-item>
        </boilerplate>
    </div>
</template>

<script>
import boilerplate from "../components/publish/boilerplate";

// 
import { $ as $http } from "../service/axios";
import { JX3BOX } from "@jx3box/jx3box-common";

const { User } = require("@jx3box/jx3box-common");
const qs = require("qs");
const lodash = require("lodash");

export default {
    name: "cj",
    props: [],
    data: function() {
        return {
            //  - 
            type: "cj",
            name: "",

            // - 
            options: {
                achievements: null,
                search_loading: false,
            },

            // - 
            post: {
                ID: "", //ID
                post_mode: "tinymce", //()
                // post_title: "",              //
                // post_content: "",            //,meta
                achievement_id: "",
                level: 3,
                remark: "",
                // post_excerpt: "",            //
                // post_tags: [],               //
                // post_banner: "",             //,
            },

            //  - 
            extend: {
                feedEnable: false, //
                followEnable: false, //
                tencentEnable: false, //
                weiboEnable: false, //
                tuilanEnable: false, //
            },
        };
    },
    computed: {},
    methods: {
        toPublish: function() {
            if (!this.post.achievement_id) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }

            if (!this.$store.state.post.post_content) {
                this.$message({ message: "", type: "warning" });
                return;
            }

            if (!this.post.remark) {
                this.$message({
                    message: "",
                    type: "warning",
                });
                return;
            }

            $http({
                method: "POST",
                url: `${JX3BOX.__helperUrl}api/achievement/${this.post.achievement_id}/post`,
                headers: { Accept: "application/prs.helper.v2+json" },
                data: qs.stringify({
                    post: {
                        level: this.post.level,
                        user_nickname: User.getInfo().name,
                        content: this.$store.state.post.post_content,
                        remark: this.post.remark,
                    },
                }),
            })
                .then((data) => {
                    data = data.data;
                    if (data.code === 200) {
                        this.$message({
                            message: "",
                            type: "success",
                            onClose: () => {
                                this.$router.push({ path: "/" });
                            },
                        });
                    } else {
                        this.$message({
                            message: `${data.message}`,
                            type: "warning",
                        });
                    }
                })
                .catch(() => {
                    this.$message({
                        message: "",
                        type: "error",
                    });
                })
                .finally(() => {
                    //this.isEditMode = false;
                });
        },
        toDraft: function() {
            console.log(this.$store.state);
        },
        get_achievement_newest_post(achievement_id) {
            return new Promise((resolve, reject) => {
                if (!achievement_id) resolve(false);
                $http({
                    url: `${JX3BOX.__helperUrl}api/achievement/${achievement_id}/post`,
                    headers: { Accept: "application/prs.helper.v2+json" },
                })
                    .then((res) => {
                        let data = res.data;
                        resolve(data.code === 200 ? data.data : false);
                    })
                    .catch((err) => {
                        resolve(false);
                    });
            });
        },
        icon_url_filter(icon_id) {
            if (isNaN(parseInt(icon_id))) {
                return `${JX3BOX.__ossMirror}image/common/nullicon.png`;
            } else {
                return `${JX3BOX.__ossMirror}icon/${icon_id}.png`;
            }
        },
        async search_achievements_handle(keyword) {
            this.options.search_loading = true;
            let data = await this.search_achievements(keyword, 10);
            this.options.achievements = data.achievements;
            this.options.search_loading = false;
        },
        // 
        search_achievements(keyword, length) {
            return new Promise((resolve, reject) => {
                $http({
                    method: "GET",
                    url: `${JX3BOX.__helperUrl}api/achievement/search`,
                    headers: { Accept: "application/prs.helper.v2+json" },
                    params: { keyword: keyword, limit: length },
                }).then(
                    function(data) {
                        data = data.data;
                        resolve(data.code === 200 ? data.data : false);
                    },
                    function() {
                        resolve(false);
                    }
                );
            });
        },
    },
    async mounted() {
        await this.search_achievements_handle("");

        // IDwatch
        let achievement_id = this.$route.params.achievement_id;
        this.post.achievement_id = achievement_id
            ? parseInt(achievement_id)
            : null;

        // 
        document.getElementsByClassName("m-publish-title").forEach((item) => {
            item.remove();
        });
        // 
        document.getElementsByClassName("el-button--plain").forEach((item) => {
            item.remove();
        });
    },
    watch: {
        "post.achievement_id": {
            async handler() {
                let data = await this.get_achievement_newest_post(
                    this.post.achievement_id
                );
                let post = data.post;
                let achievement = data.achievement;
                if (post) {
                    // 
                    this.post.achievement_id = parseInt(post.achievement_id);
                    this.post.level = post.level || 1;
                    this.post.remark = "";

                    // 
                    let _interval = setInterval(() => {
                        this.$store.state.post.post_content = "";
                        this.$store.state.post.post_content = post.content;
                        if (!post.content || tinyMCE.activeEditor.getContent())
                            clearInterval(_interval);
                    }, 200);
                } else {
                    // 
                    this.post.achievement_id = this.post.achievement_id
                        ? parseInt(this.post.achievement_id)
                        : "";
                    this.post.level = 3;
                    this.post.remark = "";
                    this.$store.state.post.post_content = "";
                }

                if (achievement) {
                    // 
                    let exist = false;
                    this.options.achievements = this.options.achievements || [];
                    for (let index in this.options.achievements) {
                        if (
                            this.options.achievements[index].ID ==
                            this.post.achievement_id
                        ) {
                            exist = true;
                            break;
                        }
                    }
                    if (!exist) this.options.achievements.push(achievement);
                }
            },
        },
    },
    components: {
        boilerplate,
    },
};
</script>

<style lang="less">
@import "../assets/css/publish/cj.less";
</style>
