<template>
    <div class="m-dashboard-publish-share">
        <!--   -->
        <!-- 
            localDraft : 
            infoEnable : 
            markdownEnable : markdown
            excerptEnable : 
            tagEnable : 
            notifyEnable : 
            bannerEnable : ,
         -->
        <boilerplate
            v-if="loaded"
            :name="name"
            :type="type"
            :post="post"
            :meta="meta"
            :extend="extend"
            :localDraft="true"
            :infoEnable="true"
            :contentEnable="false"
            :markdownEnable="false"
            :excerptEnable="false"
            :tagEnable="false"
            :notifyEnable="false"
            :bannerEnable="false"
            @publish="toPublish"
            @draft="toDraft"
        >
            <template>
                <el-form-item label="">
                    <el-radio-group v-model="post.post_subtype">
                        <el-radio
                            border
                            v-for="(type, i) in options.types"
                            :label="type"
                            :key="i"
                            >{{ type }}</el-radio
                        >
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="">
                    <el-input
                        v-model="post.post_meta.author"
                        placeholder=""
                    ></el-input>
                </el-form-item>

                <el-form-item label="">
                    <album
                        :imgList="post.post_meta.pics"
                        @albumChange="updateAlbum"
                    ></album>
                </el-form-item>

                <el-form-item label="">
                    <input
                        class="u-data-input"
                        type="file"
                        id="face_file"
                        @change="uploadData"
                    />
                    <el-button
                        type="primary"
                        @click="selectData"
                        icon="el-icon-upload2"
                        ></el-button
                    >
                    <span class="u-data-ready" v-show="post.post_meta.file">
                        <i class="el-icon-success"></i>
                        
                    </span>
                </el-form-item>

                <el-form-item label="">
                    <el-input
                        v-model="post.post_meta.data"
                        type="textarea"
                        :rows="6"
                    ></el-input>
                </el-form-item>
            </template>
        </boilerplate>
    </div>
</template>

<script>
import boilerplate from "@/components/publish/boilerplate";
import { __ossMirror } from "@jx3box/jx3box-common/js/jx3box.json";
import album from "@/components/publish/album.vue";
// import lodash from "lodash";
import { uploadData, parseData } from "../service/share.js";
const types = ["", "", "", ""];

export default {
    name: "share",
    props: [],
    data: function() {
        return {
            // - 
            type: "share",
            name: "",
            loaded: false,

            //
            options: {
                types: types,
            },

            //
            meta: {},

            // - 
            post: {
                ID: "", //ID
                // post_author               //,token
                // post_type:"",             //(boilerplate)
                post_subtype: "", //()
                post_title: "", //
                post_content: "", //,meta
                post_meta: {
                    author: "", //
                    pics: [], //
                    file: "", //
                    data: "", //
                },
                post_excerpt: "", //
                post_mode: "tinymce", //()
                post_banner: "", //,
                post_status: "", //
                // post_tags: [],            //
            },

            // - 
            extend: {
                feedEnable: false, //
                followEnable: false, //
                tencentEnable: false, //
                weiboEnable: false, //
                tuilanEnable: false, //
            },
        };
    },
    computed: {},
    methods: {
        // 
        toPublish: function() {
            // console.log(this.build());
            this.doPublish(this.build(), this).then((res) => {});
        },
        // 
        toDraft: function() {
            this.doDraft(this.build(), this).then((res) => {});
        },
        // meta
        build: function() {
            let data = this.$store.state;
            data.post.meta_1 = data.post.post_meta.author   //
            return data;
        },
        // 
        init: function() {
            return this.doLoad(this).then((data) => {});
        },

        // 
        updateAlbum: function(filelist) {
            let imglist = [];
            filelist.forEach((img) => {
                imglist.push({
                    name: img.name,
                    url: img.url,
                });
            });
            this.post.post_meta.pics = imglist;
        },
        // 
        selectData: function(i) {
            let fileInput = document.getElementById("face_file");
            fileInput.dispatchEvent(new MouseEvent("click"));
        },
        uploadData: function(e) {
            let formdata = new FormData();
            let file = e.target.files[0];
            formdata.append("file", file);
            uploadData(formdata, this).then((res) => {
                this.post.post_meta.file = res.data.data.list[0];
                this.$message({
                    message: res.data.msg,
                    type: "success",
                });
            });
            this.parseData(file);
        },
        // 
        parseData: function(facedata) {
            // 
            if (!FileReader) return;
            const vm = this;

            let fr = new FileReader();
            fr.readAsText(facedata);
            fr.onload = function(e) {
                console.log("......");
                parseData(e.target.result,vm)
                    .then((res) => {
                        vm.post.post_meta.data = JSON.stringify(res.data.data);
                        return res.data.status;
                    })
                    .then((status) => {
                        if (status) {
                            vm.$notify({
                                title: "",
                                message: "",
                                type: "success",
                            });
                        } else {
                            vm.$notify.error({
                                title: "",
                                message: "",
                            });
                        }
                    })
            };
            fr.onerror = function(e) {
                vm.$notify.error({
                    title: "",
                    message: "",
                });
            };
        },
    },
    filters: {},
    mounted: function() {
        // 
        this.init().then(() => {
            console.log("Init Post:", this.post);
        });
    },
    components: {
        boilerplate,
        album,
    },
};
</script>

<style lang="less">
@import "../assets/css/publish/share.less";
</style>
