<template>
    <div class="m-dashboard-publish-macro">
        <!--   -->
        <!-- 
            localDraft : 
            infoEnable : 
            markdownEnable : markdown
            excerptEnable : 
            tagEnable : 
            notifyEnable : 
            bannerEnable : ,
         -->
        <boilerplate
            v-if="loaded"
            :name="name"
            :type="type"
            :post="post"
            :meta="meta"
            :extend="extend"
            :localDraft="true"
            :infoEnable="true"
            :contentEnable="true"
            :markdownEnable="false"
            :excerptEnable="true"
            :tagEnable="false"
            :notifyEnable="true"
            :bannerEnable="false"
            @publish="toPublish"
            publish_text=""
            @draft="toDraft"
            draft_text=""
        >
            <!--   -->
            <template>
                <!-- 1. -->
                <el-form-item label="">
                    <el-select
                        class="m-publish-zlp m-macro-zlp"
                        v-model="post.post_meta.zlp"
                        filterable
                        placeholder=""
                    >
                        <el-option
                            v-for="item in options.zlps"
                            :key="item"
                            :label="item"
                            :value="item"
                        >
                        </el-option>
                    </el-select>
                </el-form-item>

                <!-- 2. -->
                <el-form-item label="" class="m-publish-xf m-macro-xf">
                    <el-radio
                        v-for="(item, i) in options.xfmap"
                        v-model="post.post_subtype"
                        :label="item.name"
                        :key="i"
                        @change="changeSubtype"
                    >
                        <img
                            class="u-pic"
                            :src="item.id | xficon"
                            :alt="item.name"
                        />
                        <span class="u-txt">{{ item.name }}</span>
                    </el-radio>
                </el-form-item>

                <!-- 3. -->
                <el-divider content-position="left"></el-divider>
                <div class="m-macro-box">
                    <div class="m-macro-header">
                        <el-button
                            class="m-macro-addbutton"
                            icon="el-icon-circle-plus-outline"
                            type="primary"
                            @click="addMacro"
                            ></el-button
                        >
                        <a
                            class="m-macro-docs el-button el-button--primary is-plain el-button--small"
                            target="_blank"
                            href="https://www.jx3box.com/tool/265/"
                            ><i class="el-icon-s-management"></i>
                            </a
                        >
                        <a
                            class="m-macro-help el-button el-button--success is-plain el-button--small"
                            href="https://www.jx3box.com/tool/14671/"
                            target="_blank"
                        >
                            <i class="el-icon-info"></i> 
                        </a>
                    </div>

                    <el-tabs
                        v-model="activeMacroIndex"
                        type="card"
                        closable
                        @tab-remove="removeMacro"
                    >
                        <el-tab-pane
                            v-for="(item, i) in post.post_meta.data"
                            :key="i"
                            :name="i + 1 + ''"
                        >
                            <span slot="label"
                                ><img class="u-tabicon" :src="icon(item)" />{{
                                    i + 1 + "-" + item.name
                                }}</span
                            >
                            <div class="m-macro-cloud m-macro-item">
                                <h5 class="u-title">
                                    /
                                    <a
                                        class="u-icon-links"
                                        href="/app/icons"
                                        target="_blank"
                                        ><i class="el-icon-question"></i>
                                        </a
                                    >
                                </h5>
                                <div class="u-group">
                                    <div class="u-subblock m-macro-icon">
                                        <el-input
                                            v-model="item.icon"
                                            placeholder="ID"
                                            :minlength="1"
                                            :maxlength="10"
                                            :max="30000"
                                            :min="0"
                                        >
                                            <template slot="prepend">
                                                <img
                                                    class="u-icon"
                                                    :src="icon(item)"
                                                />
                                            </template>
                                        </el-input>
                                    </div>
                                    <div class="u-subblock m-macro-name">
                                        <el-input
                                            v-model="item.name"
                                            placeholder=""
                                            :minlength="1"
                                            :maxlength="20"
                                            show-word-limit
                                            @change="checkDataName(item)"
                                        >
                                            <template slot="prepend"
                                                ><b class="u-feed"
                                                    >{{ nickname }}#{{
                                                        item.name
                                                    }}</b
                                                ></template
                                            >
                                        </el-input>
                                    </div>
                                </div>
                            </div>
                            <div class="m-macro-talent m-macro-item">
                                <h5 class="u-title">
                                    
                                </h5>
                                <div class="m-macro-talent-simulator">
                                    <div class="qx-container"></div>
                                </div>
                                <el-input
                                    v-model="item.talent"
                                    placeholder=""
                                    @change="checkTalent(item)"
                                    ><template slot="prepend"
                                        ><a
                                            class="u-get"
                                            target="_blank"
                                            href="/app/talent"
                                            ><i class="el-icon-warning"></i>
                                            </a
                                        ></template
                                    ></el-input
                                >
                            </div>
                            <div class="m-macro-macro">
                                <h5 class="u-title">
                                    
                                    <a
                                        class="u-icon-links"
                                        href="/app/macroeditor"
                                        target="_blank"
                                        ><i class="el-icon-question"></i>
                                        </a
                                    >
                                </h5>
                                <el-input
                                    v-model="item.macro"
                                    placeholder=","
                                    :minlength="1"
                                    :maxlength="128"
                                    show-word-limit
                                    type="textarea"
                                    :rows="12"
                                >
                                </el-input>
                            </div>
                            <el-form-item label="" class="m-macro-misc">
                                <el-row>
                                    <el-col :span="8" class="u-speed"
                                        ><el-input
                                            v-model="item.speed"
                                            placeholder=""
                                        >
                                            <template slot="prepend"
                                                ></template
                                            >
                                        </el-input></el-col
                                    >
                                    <el-col :span="8" class="u-equip"
                                        ><el-input
                                            v-model="item.equip"
                                            placeholder=","
                                            disabled
                                            ><template slot="prepend"
                                                ></template
                                            ></el-input
                                        ></el-col
                                    >
                                    <el-col :span="8"></el-col>
                                </el-row>
                            </el-form-item>
                            <el-form-item label="" class="m-macro-desc">
                                <el-input
                                    v-model="item.desc"
                                    type="textarea"
                                    placeholder=".."
                                ></el-input>
                            </el-form-item>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </template>
        </boilerplate>
    </div>
</template>

<script>
import boilerplate from "@/components/publish/boilerplate";
import xfmap from "@jx3box/jx3box-data/data/xf/xf.json";
import { __ossMirror } from "@jx3box/jx3box-common/js/jx3box.json";
import User from "@jx3box/jx3box-common/js/user";
import { syncRedis } from "../service/macro.js";
import { sterilizer } from "sterilizer/index.js";
import lodash from "lodash";
import zlps from "../assets/data/zlps.json";

export default {
    name: "macro",
    props: [],
    data: function() {
        return {
            // - 
            type: "macro",
            name: "",
            loaded: false,

            //
            options: {
                zlps: zlps,
                xfmap: xfmap,
            },

            //
            meta: {},

            // - 
            post: {
                ID: "", //ID
                // post_author               //,token
                // post_type:"",             //(boilerplate)
                post_subtype: "", //()
                post_title: "", //
                post_content: "", //,meta
                post_meta: {
                    zlp: zlps[0],
                    data: [
                        {
                            name: "",
                            icon: 13,
                            talent: "",
                            macro: "",
                            speed: "",
                            equip: "",
                            desc: "",
                        },
                    ],
                },
                post_excerpt: "", //
                post_mode: "tinymce", //()
                post_banner: "", //,
                post_status: "", //
                // post_tags: [],            //
            },

            // - 
            extend: {
                feedEnable: false, //
                followEnable: false, //
                tencentEnable: false, //
                weiboEnable: false, //
                tuilanEnable: false, //
            },

            // 
            activeMacroIndex: "1",
            nickname: User.getInfo().name,
        };
    },
    computed: {},
    methods: {
        // 
        toPublish: function() {
            // console.log(this.build());
            this.doPublish(this.build(), this, false).then((res) => {
                syncRedis(res.data.data, this)
                    .then((redis_result) => {
                        this.finish(res.data.msg, res.data.data.ID, this.type);
                    })
                    .catch((err) => {
                        this.failCallback(err, this);
                    });
            });
        },
        // 
        toDraft: function() {
            this.doDraft(this.build(), this, false).then((res) => {
                syncRedis(res.data.data, this)
                    .then((redis_result) => {
                        this.finish(res.data.msg, res.data.data.ID, this.type);
                    })
                    .catch((err) => {
                        this.failCallback(err, this);
                    });
            });
        },
        finish: function(msg, id, type) {
            this.$message({
                message: msg,
                type: "success",
            });
            setTimeout(() => {
                location.href = "/" + type + "/?pid=" + id;
            }, 500);
        },
        // 
        init: function() {
            return this.doLoad(this).then((data) => {});
        },
        // meta
        build: function() {
            let data = this.$store.state;
            data.post.meta_1 = data.post.post_meta.zlp; //
            data.post.meta_2 = ~~lodash.get(
                xfmap[data.post.post_subtype],
                "id"
            ); //id
            return data;
        },

        // 
        addMacro: function() {
            if (this.post.post_meta.data.length > 7) {
                this.$alert("", "", {
                    confirmButtonText: "",
                });
                return;
            }

            let index = this.post.post_meta.data.length + 1 + "";
            this.post.post_meta.data.push({
                name: "",
                icon: 13,
                talent: "",
                macro: "",
                speed: "",
                equip: "",
                desc: "",
            });
            this.activeMacroIndex = index;
        },
        // 
        removeMacro: function(name) {
            if (this.post.post_meta.data.length < 2) {
                this.$alert("1", "", {
                    confirmButtonText: "",
                });
                return;
            }

            this.$alert("", "", {
                confirmButtonText: "",
                callback: (action) => {
                    if(action == 'confirm'){
                        // 
                        let i = ~~name - 1;
                        this.post.post_meta.data.splice(i, 1);
                        // focus
                        this.activeMacroIndex = i + "";
                    }
                },
            });
        },

        // 
        checkDataName: function(data) {
            let name = sterilizer(data.name).removeSpace();
            if (!name) {
                this.$notify.error({
                    title: "",
                    message: "",
                });
                return;
            }
            name = sterilizer(name).kill();
            this.$set(data, "name", name);
        },
        checkTalent: function(data) {
            try {
                JSON.parse(data.talent);
            } catch (e) {
                this.$notify.error({
                    title: "",
                    message: "",
                });
            }
        },

        // 
        icon: function(item) {
            let id = isNaN(item.icon) ? 13 : ~~item.icon;
            id = Math.max(0, Math.min(id, 30000));
            this.$set(item, "icon", id);
            return __ossMirror + "icon/" + id + ".png";
        },
        changeSubtype: function() {
            let iconid = xfmap[this.post.post_subtype]["icon"];
            this.$set(this.post.post_meta.data[0], "icon", iconid);
        },
    },
    filters: {
        xficon: function(id) {
            return __ossMirror + "image/xf/" + id + ".png";
        },
    },
    mounted: function() {
        // 
        this.init().then(() => {
            console.log("Init Post:", this.post);
        });
    },
    components: {
        boilerplate,
    },
};
</script>

<style lang="less">
@import "../assets/css/publish/macro.less";
</style>
